import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AccountService} from '../account.service';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent implements OnInit {

  labelSize = 6;
  inputSize = 16;
  validateForm: FormGroup;
  gender: string;

  constructor(private fb: FormBuilder,
              private accountService: AccountService) {
  }

  ngOnInit() {
    this.validateForm = this.fb.group({
      email: [null, Validators.required],
      password: [null, Validators.required],
      checkPassword: [null, Validators.required],
      real_name: [null, Validators.required],
      nickname: [null, Validators.required],
      idNumber: [null, Validators.required],
      birthday: [null, Validators.required],
      phoneNumber: [null, Validators.required],
      address: [null, Validators.required],
      gender: [null, Validators.required],
    });
    this.gender = '男';
  }

  submitForm() {
    for (const i in this.validateForm.controls) {
      if (i) {
        this.validateForm.controls[i].markAsDirty();
        this.validateForm.controls[i].updateValueAndValidity();
      }
    }
    if (!this.validateForm.valid) {
      return;
    }
    const email = this.validateForm.get('email').value;
    const password = this.validateForm.get('password').value;
    const nickname = this.validateForm.get('nickname').value;
    const real_name = this.validateForm.get('real_name').value;
    const birthday = this.validateForm.get('birthday').value;
    const phoneNumber = this.validateForm.get('phoneNumber').value;
    const idNumber = this.validateForm.get('idNumber').value;
    const address = this.validateForm.get('address').value;
    this.accountService.register(email, nickname, password, this.gender,
      real_name, idNumber, phoneNumber, address, birthday);
  }
}
