import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {host} from './config';

@Injectable({
  providedIn: 'root'
})
export class AccountService {

  constructor(private httpClient: HttpClient) {
  }

  login(eMail: string, password: string) {
    const loginURL = host + 'api/register';
    const data = {
      E_mail: eMail,
      passwd: password,
    };
    return this.httpClient.post(loginURL, data, this.getHttpOption());
  }

  register(email: string, name: string, password: string, gender: string,
           real_name: string, idNumber: string,
           phone_number: string, address: string, birthday) {
    const registrationURL = host + 'api/register';
    const data = {
      mail_address: email,
      user_name: name,
      user_password: password,
      user_gender: gender,
      user_real_name: real_name,
      user_sfz: idNumber,
      user_phone: phone_number,
      user_dizhi: address,
      user_birthday: birthday,
    };
    this.httpClient.post(registrationURL, data, this.getHttpOption()).subscribe(
      response => {
        console.log(response);
      }
    );
  }

  private getHttpOption() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
      })
    };
    return httpOptions;
  }
}
