import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AccountService} from '../account.service';

@Component({
  selector: 'app-tool-bar',
  templateUrl: './tool-bar.component.html',
  styleUrls: ['./tool-bar.component.css']
})
export class ToolBarComponent implements OnInit {
  loginFormIsVisible = false;
  registrationFormIsVisible = false;

  validateLoginForm: FormGroup;

  validateRegisterForm: FormGroup;

  constructor(private fb: FormBuilder,
              private accountService: AccountService) {
  }

  openLoginForm() {
    this.loginFormIsVisible = true;
  }

  closeRegistrationForm() {
    this.registrationFormIsVisible = false;
  }

  ngOnInit() {
    this.validateLoginForm = this.fb.group({
      userName: [null, [Validators.required]],
      password: [null, [Validators.required]],
    });
    this.validateRegisterForm = this.fb.group({
      userName: [null, [Validators.required]],
      password: [null, [Validators.required]],
    });
  }

  submitLoginForm() {
    for (const i in this.validateLoginForm.controls) {
      if (i) {
        this.validateLoginForm.controls[i].markAsDirty();
        this.validateLoginForm.controls[i].updateValueAndValidity();
      }
    }
    if (!this.validateLoginForm.valid) {
      return;
    }
    const eMail = this.validateLoginForm.get('userName').value;
    const password = this.validateLoginForm.get('password').value;
    const $response = this.accountService.login(eMail, password).subscribe(
      response => {
        console.log(response);
        $response.unsubscribe();
      }
    );
  }

  closeLoginForm() {
    this.loginFormIsVisible = false;
  }
}
